<template>
  <div id="app">
    <keep-alive>
    
    <router-view  ></router-view>
   
    </keep-alive>
  </div>
</template>

<script>
let Base64 = require('js-base64').Base64
let AES = require("crypto-js/aes");
let  CryptoJS = require("crypto-js");

export default {
  name: 'app',
   mounted() {
       loading.outerHTML=""
       if(localStorage.getItem("user")){
          this.$store.state.loginedUser=JSON.parse(localStorage.getItem('user'))
       }
       else {
        if(!localStorage.getItem("canView")){
           localStorage.setItem("canView","true")
         }
       }
       if (localStorage.canView=="false"){
         this.$router.push({path:"/login"})
       }
       this.$http.get(this.$store.state.serverUrl+"/api/config").then(response=>{
      
       let result=JSON.parse(Base64.decode(response.body))

       this.$store.state.showinfo=result['showinfo']
       this.$store.state.freetime=result['guesttime']*1000*60
       this.$store.state.showTip=result['showTip']
       this.$store.state.bookkeys=result['books']
       this.$store.state.livekeys=result['lives']
       this.$store.state.photokeys=result['photos']
       this.$store.state.videokeys=result['movies']
       
       setTimeout(this.freeend,this.$store.state.freetime)
       },response=>{
       setTimeout(this.freeend,this.$store.state.freetime)
      })
    },
   methods: {
     canView() {
    if(!localStorage.getItem("canView")){
      return false
    }
    return localStorage.getItem("canView")=="true"
  },
     freeend(){
      
      if(!localStorage.getItem("user")){
        localStorage.setItem("canView","false");
        this.$router.push({path:"/login"})
        alert("您的体验时间已到 "+this.$store.state.showinfo)
      }
      
     }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  color: #2c3e50;
}
.child-view {
  position: absolute;
  width:100%;
  transition: all .8s cubic-bezier(.55,0,.1,1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(50px, 0);
  transform: translate(50px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-50px, 0);
  transform: translate(-50px, 0);
}

</style>



