<template>
  <div style="position:fixed;margin:0px;height:100%;width:100%;
  background-color: #c86dd7;
         background: -moz-linear-gradient(-45deg,  #3023ae 0%, #c86dd7 100%);
        background: -webkit-gradient(linear, left top, right bottom, color-stop(0%,#3023ae), color-stop(100%,#c86dd7));
        background: -webkit-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%) ;
        background: -o-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
        background: -ms-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
        background: linear-gradient(135deg,  #3023ae 0%,#c86dd7 100%);
        background-attachment: fixed;  
  
  ">
   <mu-appbar  title="登录">
   <mu-icon-button icon="navigate_before" @click="goback()" slot="left" />
 </mu-appbar>
 
 <div class="center">

  <img src="https://ooo.0o0.ooo/2017/06/24/594e330a587a5.jpg" style="width:48px;">
  <mu-text-field  label="账号" hintText="英文和数字组合，建议用邮箱，方便找回密码" fullWidth   v-model="username"  labelFloat/><br/>
  <mu-text-field  label="密码" hintText="请输入密码，至少6位" fullWidth  type="password" v-model="password" labelFloat/><br/>
  <mu-text-field  v-if="noAccount"  label="邀请码" hintText="输入注册邀请码,若无保持为空" fullWidth   labelFloat  v-model="icode"/>
  
  <br><br>
  <mu-raised-button  v-if="noAccount" label="注册" @click="register()"  fullWidth  primary/>
  <mu-raised-button v-if="!noAccount" label="登陆" @click="login(1)"  fullWidth primary/>
  </br>
  </br>
  <div style="display:flex;justify-content:center;align-items:center">
   <mu-flat-button  :label="noAccount?'  立即登录':'注册'" v-model="noAccount" @click="noAccount=!noAccount" />
    <h5>|</h5>
   <mu-flat-button  :label="noAccount?'获取邀请码':'忘记密码?'" v-model="noAccount" @click="tip()" />
   </div>
  <br></br>
  <h5>注册登陆即代表您同意本服务和隐私条款</h5>
  
</div>

</div>
</template>
<script >
  export default {
    name:'login',
    data(){
      return {
       noAccount:true,
       username:"",
       password:"",
       icode:""
     }
   },
   
   methods: {

    login(usertype)
    {   
      if(usertype==0){

        this.username=null
        this.password=null
        this.icode=null
        
        if(!localStorage.getItem("canView")){

           alert("已经体验10分钟了")
           this.noAccount=true
           return
        }
        
      }

      if(this.username!="" &&this.password!="") {
        
           this.$http.post(this.$store.state.serverUrl+"/login",{username:this.username,password:this.password},{
                            headers: {'Content-Type':'application/json'}
                        }).then(
                   response=>{ 
                   
                    if(response.body['token']){
                      localStorage.user=JSON.stringify(response.body)
                      this.$store.state.loginedUser=response.body
                     
                      localStorage.setItem("canView",false)
                      this.$router.push({path:'/index'}) 
                     
                    } 
                  else {
                     alert("登陆失败，请确认你的用户名密码正确！")
                  }
                },response=>{
                  alert(response.body)
              
          })  
      }
      else {
        alert("用户名密码不能为空！")
      }
      
          //this.$router.push({path:'/index'})
          
        },
        register()
        {  
           if(this.username!=""&&this.password!=""){
               this.$http.post(this.$store.state.serverUrl+"/register",{username:this.username,password:this.password,icode:this.icode},{
                            headers: {'Content-Type':'application/json; charset=UTF-8'}
                        }).then(
                response=>{ 
                
                  if(response.body['status']=="success"){
                    
                    alert("注册成功！您的账户为:"+this.username+"密码为："+this.password+",请牢记！")
                    this.noAccount=false
                  } 
                  else {
                    alert(response.body['msg'])
                  }
                },response=>{
                  alert(response.body['msg'])
                })
           }
           else {
               alert("用户名密码不能为空！")
           } 
        },
        goback()
        {
          this.$router.push({path:"/index"})
        },
        tip(){
           alert(this.$store.state.showinfo)
        }
        
      } 
    }
  </script>
  <style scoped>
    
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
  }
  .center{
   margin: 50px;
   margin-top: 80px;
   
   text-align: center;
   
 }
 .desc-container{
  height:100%;
  overflow-y: scroll;
}
img {
  width: 100%;
}
.fixed {
  position:fixed;
  top: 0;

}

</style>


