import Vue from "vue"

import Vuex from "vuex"

Vue.use(Vuex)

const state ={
	cvo:null,
	pvo:null,
	book:null,
	vipvo:null,
	liveobj:null,
	loginedUser:null,
	serverUrl:"http://xyz1818.xyz",
	freetime:1000*60*5,
	showTip:true,
	videokeys:null,
	livekeys:null,
	bookkeys:null,
	photokeys:null,
}

const mutations={
	changevideo(state,vo)
	{
		state.cvo=vo
	},
	changemm(state,vo)
	{
		state.pvo=vo
	},
	changebook(state,vo)
	{
		state.book=vo
	},
	changeVipVideo(state,vo)
	{
		state.vipvo=vo
	},
	changeLiveVideo(state,vo)
	{
		state.liveobj=vo
	},
	changeUser(state,vo){
		state.loginedUser=vo
	}
}
const actions={
	changevideo({commit},vo) {
		commit('changevideo',vo)
	},
	changemm({commit},vo){
		commit('changemm',vo)
	},
	changebook({commit},vo){
		commit('changebook',vo)
	},
	changeVipVideo({commit},vo){
		commit("changeVipVideo",vo)
	},
	changeLiveVideo({commit},vo){
		commit("changeLiveVideo",vo)
	},
	changeUser({commit},vo){
		commit("changeUser",vo)
	}
}
const getters={
	getVo(state){
		return state.cvo
	},
	getPvo(state){
		return state.pvo
	},
	getBook(state){
		return state.book
	},
	getVip(state){
		return state.vipvo
	},
	getLive(state){
		return state.liveobj
	},
	getUser(state){
		return state.loginedUser
	}

}

export default new Vuex.Store({
	state,
	mutations,
	actions,
	getters
})




